
import { Link,BrowserRouter} from 'react-router-dom'
import {Route} from 'react-router';
import Codesnippet from "../VScodesnippet/Codesnippet";
//import Codesnippet from '../general/GeneralComponentStyles';
import { useHistory } from "react-router-dom";

var Homepage = ()=>{

  <BrowserRouter >
    <Route path="/codesnippet" component={Codesnippet} />
  </BrowserRouter>
  const history = useHistory();

  const redirectToCodeSnippet=()=>{
   
    history.push('/');
  }
    return(
        <>
    <div className="container mx-auto px-764  w-7/12 p-200">
    <div class="decoration-clone md:decoration-slice ...">
    <h1 className="text-4xl text-grey-500  font-bold leading-normal mt-0 mb-2 ">
    UX - StyleGuide
  </h1>
  <p class="text-lg font-light leading-relaxed mt-6 mb-4 text-pink-800">
  Lorem ipsum, dolor sit amet consectetur adipisicing elit. Labore quam sunt ut? Dignissimos voluptate perspiciatis suscipit magnam voluptatibus magni veritatis. Impedit quisquam dignissimos mollitia, amet tempore commodi placeat ipsam vero.
  </p>
  <button className="bg-gradient-to-b from-blue-600 to-blue-400 border  hover:bg-blue-500 hover:text-black font-bold uppercase text-sm px-6 py-3 rounded  mr-1 mb-1 ease-linear transition-all" type="button">
  Download starterkit
</button>

<button className="text-blue-500 bg-transparent border border-solid border-blue-500 hover:bg-blue-500 hover:text-white active:bg-blue-600 font-bold uppercase text-sm px-6 py-3 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" type="button" onClick={redirectToCodeSnippet}> 
  React Component
</button>


</div>
    </div>
        </>
    );
}
export default Homepage;